.thanks {
  margin: 0 auto;
  text-align: center;
  width: 450px;
  padding-top: 4em;
  padding-bottom: 4em;
  border: 2px dashed #ccc;
}

@media only screen and (max-width: 600px) {
  .thanks {
    margin: 0 auto;
    text-align: center;
    width: 100%;
    border: 2px dashed #ccc;
    padding-top: 4em;
    padding-bottom: 4em;
  }
}
